    border-radius: 0;
    border: none;
    padding: 15px;
    text-transform: uppercase;
}
.entry-content.row h3 {font-size: 16px;}

 .cont_form .left_side{ background:#fff;  padding-top: 30px;}
 .cont_form .left_side ul li{font-size:10px;text-transform:uppercase;color:#989595;font-weight:600;padding-left:70px;padding-bottom:30px;margin-bottom:30px;position:relative;border-bottom:1px solid #e7e7e7;width:80%; list-style:none}
.cont_form .left_side ul li:before{font-family: FontAwesome;font-size: 25px;color: #337ab7;height: 55px;width: 55px;position: absolute;top: 0;left: 0;text-align: center;padding: 13px 0px;line-height: 27px;}
 .cont_form .left_side ul li:nth-child(1):before{content:"\F095"}
 .cont_form .left_side ul li:nth-child(2):before{content:"\F0E0"}
.cont_form .left_side ul li:nth-child(3):before{content:"\F041"}
 .cont_form .left_side ul li span{display:block;font-size:17px;color:#434343;text-transform:none;text-transform:initial}
 .cont_form .left_side ul li a{color:#989595;}
 .cont_form .right_side{border-left: 1px solid #e7e7e7;padding-left:6em}
.cont_form .right_side form .form-control{height: 50px;border-radius: 0;border: none;border-bottom: 1px solid #d4d4d4;box-shadow: none;font-weight: 400;margin: 15px 0px;padding: 10px 0px; color:#000;}
 .cont_form .right_side form textarea.form-control{height: 100px}
 .cont_form .right_side form .btn.btn-default{width: 100%;color: #693348;background: #feb3d1;border: none;padding: 15px 0px;font-size: 20px;text-transform: uppercase;font-weight: 800;margin: 30px 0px 10px;border-radius: 0px;}
 .cont_form .right_side form .btn.btn-default:hover{color: #feb3d1;background: #693348;}

