/*   contact fancy code */
.has-float-label { position: relative;}
.has-float-label .label-text{position: absolute;left:0;top:-15px;cursor:text;opacity: 1;-webkit-transition: all .2s;transition: all .2s;left:10px;z-index: 3;line-height: 1;padding:0 1px;font-weight:normal;font-size:12px;margin:0;color:#1a73e8; background:#fff;}
.has-float-label .label-text::after{content: " ";display: block;position: absolute;background: #fff;height: 2px;top: 80%;left: -.2em;right: -.2em;z-index: -1;}
.has-float-label .form-control::-webkit-input-placeholder {opacity: 1;-webkit-transition: all .2s;transition: all .2s;}
.has-float-label .form-control::-moz-placeholder {opacity: 1;-webkit-transition: all .2s;transition: all .2s;}
.has-float-label .form-control::-ms-input-placeholder {opacity: 1;-webkit-transition: all .2s;transition: all .2s;}
.has-float-label .form-control::placeholder {opacity: 1;-webkit-transition: all .2s;transition: all .2s;}
.has-float-label .form-control:placeholder-shown:not(:focus)::-webkit-input-placeholder {opacity: 0;}
.has-float-label .form-control:placeholder-shown:not(:focus)::-moz-placeholder {opacity: 0;}
.has-float-label .form-control:placeholder-shown:not(:focus)::-ms-input-placeholder {opacity: 0;}
.has-float-label .form-control:placeholder-shown:not(:focus)::placeholder {opacity: 0;}
.has-float-label .form-control:placeholder-shown:not(:focus) + * {opacity:1 ;top: 15px; text-transform:uppercase; color:#000;}




